<template>
  <div class="h-screen w-screen bg-gray-100 text-[12px] p-1 overflow-auto">
    <titleHeader title="发布" :goRoute="() => {
      router.go(-1)
    }"></titleHeader>
    <div class="bg-white  p-1 rounded my-1">
      <div class="flex justify-between">
        <span>参与话题</span>
        <div>
          <span class="mr-1 text-gray-500">更多</span>
          <van-icon name="arrow" size="0.4rem"></van-icon>
        </div>
      </div>
      <div class="flex  items-center justify-between my-1  ">
        <div @click="selectChange(index)" :class="{ 'border': select == index }" v-for="(value, index) in data"
          :key="value.title" class="w-[40%] bg-gray-100 p-1 rounded  truncate relative
                border-orange-400
                before:content-['官方']
                before:bg-orange-400
                before:absolute before:-top-0.5 before:right-0 before:text-white
                before:p-0.5
                ">
          # {{ value.title }}
        </div>
      </div>
    </div>
    <div class="bg-white p-1 border-gray-200 h-auto">
      <van-field v-model="comment" placeholder="请输入内容" maxlength="1000" rows="2" show-word-limit label="#食谱#"
        type="textarea"></van-field>
      <hr class="text-gray-200">
      <div class="my-1">
        <van-uploader :max-count="30" v-model="fileList">
          <template #preview-cover="{ file }">
            <div class="preview-cover van-ellipsis">{{ file.name }}</div>
          </template>
        </van-uploader>
      </div>
      <!-- <div class="flex mt-1">
        <div @click="uploadImage" class="flex mr-1 flex-col">
          <van-icon name="photo-o" size="0.5rem" color="gray" class="mb-0.3"></van-icon>
          <span>图片</span>
        </div>
        <div @click="uploadVedio" class="flex flex-col">
          <van-icon name="tv-o" size="0.5rem" color="gray" class="mb-0.3"></van-icon>
          <span>视频</span>
        </div>
      </div> -->
      <div class="mt-1 inline-block rounded p-1 bg-orange-400">
        现在支持发布30张图片啦~
      </div>
      <div class="flex justify-center items-center mt-1">
        <van-button round :color="btnColor" class=" w-15" :disabled="!(btnColor == 'green')"
          @click="upload">发布</van-button>
      </div>
      <!-- </div> -->
    </div>
  </div>
</template>
<script setup lang='ts'>
import titleHeader from '@/components/titleHeader.vue';
import { useRouter } from 'vue-router';
import { ref, watch } from 'vue';
import { topicDetail as TopicDetail } from '@/store/topicDetail'
import { publish as Publish } from '@/request/publish/index'
import { showSuccessToast } from 'vant';
import 'vant/es/toast/style'
const router = useRouter()
const comment = ref<string>('')
const btnColor = ref('#eee')
const select = ref(0)
const topicDetail = TopicDetail()
const selectChange = (index: number) => {
  select.value = index
}
const upload = () => {
  console.log(fileList.value[0])
  const formData = new FormData()
  formData.append('comment', comment.value)
  fileList.value.forEach((item, index) => {
    formData.append(`file${index + 1}`, item.file)
  })
  Publish('/api/publish', 'post', formData).then((res) => {
    if (res.status == 200) {
      console.log(res.data.message)
      showSuccessToast(res.data.message)
      router.go(-1)
    }
  })
}
onMounted(() => {
  data.value.forEach((item, index) => {
    if (item.title == topicDetail.topicDetail.title) {
      select.value = index
    }
  })
})
const data = ref([{
  title: '食谱',
  offical: true,
  // select: true,
}, {
  title: '寻找最美班级',
  offical: true,
  // select: false
}])
const fileList = ref([])
watch(comment, () => {
  // console.log(newVal, oldVal)
  if (comment.value.length > 0) {
    btnColor.value = 'green'
  }
  else {
    btnColor.value = '#eee'
  }
})
</script>
<style scoped lang=''>

</style>
